<template>
  <div class="info">
    <!-- 主图 -->
    <div class="mainImg">
      <img :src="'http://47.93.224.9/static/uploads/'+list.image" alt />
    </div>
    <!-- 商品 -->
    <div class="infobuy">
      <h3 class="title">{{list.name}}</h3>
      <p class="money">
        定价：
        <span>${{list.id}}</span>
      </p>
      <span>购买数量：</span>
      <div class="mui-numbox">
        <button class="mui-btn mui-numbox-btn-minus" type="button" @click="reduce">-</button>
        <input class="mui-numbox-input" type="number" v-model="number" />
        <button class="mui-btn mui-numbox-btn-plus" type="button" @click="add">+</button>
      </div>
      <div class="buy">
        <button @click="buy">立即购买</button>
        <button @click="join">加入购物车</button>
      </div>
    </div>
    <!-- 商品参数 -->
    <div class="goodsParams">
      <h3 class="title">商品参数</h3>
      <p>
        商品卖点:
        <span>{{list.sell_point}}</span>
      </p>
      <p>
        库存情况:
        <span>{{list.num}}</span>
      </p>
      <p>
        上架时间:
        <span>{{list.create_time}}</span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: "Info",
  data() {
    return {
      list: "",
      number: 1
    };
  },
  mounted() {
    this.$axios
      .get("http://47.93.224.9/api/goodsinfo?id=" + this.$route.query.id)
      .then(res => {
        this.list = res.data.data;
      });
  },
  methods: {
    add() {
      this.number++;
    },
    reduce() {
      if (this.number > 1) {
        this.number--;
      }
    },
    buy() {
      // this.$router.push('/shopCart')
    },
    join() { //加入购物车
      this.$store.commit('cartadd',{id:this.list.id,count:this.number,bool:true})
    }
  }
};
</script>

<style>
.info {
  padding: 10px;
}
.mainImg {
  height: 13rem;
  background: #ffffff;
  text-align: center;
}
.infobuy {
  margin: 20px 0;
  padding: 10px 20px;
  background: #ffffff;
  text-align: left;
}
.title {
  line-height: 45px;
  padding-bottom: 10px;
  border-bottom: 1px solid #9b9b9b;
  color: #333;
}
.infobuy .money {
  font-size: 19px;
  color: #333333;
  line-height: 45px;
}
.infobuy .money span {
  color: rgb(216, 0, 0);
  font-weight: bold;
}
.buy {
  text-align: center;
  margin-top: 10px;
}
.buy > button {
  border-radius: 5px;
  color: #fff;
  padding: 10px;
}
.buy > button:first-child {
  background: #4aa2f9;
  margin-right: 10px;
}
.buy > button:last-child {
  background: #e85251;
}

.goodsParams {
  background: #fff;
  padding-left: 10px;
  padding-bottom: 10px;
  height: auto;
  text-align: left;
}
.goodsParams > p {
  color: #444;
}
.goodsParams > p > span {
  margin-left: 10px;
}
</style>